import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import { connect } from "react-redux";
import { editkategoriList, getkategoriList } from "../actions/kategoriAction";
import notrespond from "../Images/500.svg";
import Swal from "sweetalert2";
import FormKategori from "./Component/FormKategori";
import FormKategoriEdit from "./Component/FormKategoriEdit";
import { Link } from "react-router-dom";
import { reset } from "redux-form";
import Skeleton from "react-loading-skeleton";
import { AxiosMasterDelete, AxiosMasterPost, AxiosMasterPut } from "../Axios";
import { berhasil, gagal, LogData, reactive } from "../Alert";
import { hideModal, showModal } from "../actions/ModalAction";
import ModalMasterData from "./Component/ModalMasterData";
import HeaderCard from "../HeaderCard";
import TourGuide from "../TourGuide";
import { finishSend, progressSend } from "../actions/LogicAction";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;

function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      AxiosMasterDelete("kategori/delete/" + kode)
        .then((res) =>
          berhasil("Data Berhasil Dihapus!").then(() =>
            dispatch(getkategoriList())
          )
        )
        .catch((err) => gagal("Data Gagal Dihapus!"));
    }
  });
}

const maptoState = (state) => {
  return {
    kategoriList: state.provinsi.kategori,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    onSend: state.provinsi.onSend,
  };
};

class Kategori extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "kode_kategori",
          text: "Kode Kategori",
        },
        {
          dataField: "nama_kategori",
          text: "Nama Kategori",
        },
        {
          dataField: "kode_group",
          text: "Kode Group",
        },
        {
          dataField: "link",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  data-tut="reactour_3"
                  onClick={() => hapus(this.props.dispatch, row.kode_kategori)}
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
                <button
                  className="btn btn-warning mr-2"
                  onClick={() =>
                    this.props.dispatch(
                      editkategoriList(
                        row.kode_kategori,
                        row.nama_kategori,
                        row.kode_group,
                        this.editModal()
                      )
                    )
                  }
                >
                  <i className="fa fa-edit mr-1"></i>
                  Edit
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getkategoriList());
  }

  handleSubmit(hasil) {
    let data = {
      kode_kategori: hasil.kodeKategori,
      nama_kategori: hasil.keterangan,
      kode_group: hasil.kodeGroup,
    };

    this.props.dispatch(progressSend());
    AxiosMasterPost("kategori/add", data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Ditambah!")
          .then(() => this.props.dispatch(getkategoriList()))
          .then(() => this.props.dispatch(reset("Kategori")))
      )
      .catch((err) =>
        this.handleReactive(
          err,
          hasil.kodeKategori,
          {
            nama_kategori: hasil.keterangan,
            kode_group: hasil.kodeGroup,
          },
          this.props.dispatch(finishSend())
        )
      );
  }
  handleEdit(hasil) {
    let data = {
      nama_kategori: hasil.keterangan,
      kode_group: hasil.kodeGroup,
    };
    this.props.dispatch(progressSend());
    AxiosMasterPut(
      "kategori/update/by-kode-kategori/" + hasil.kodeKategori,
      data
    )
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Diubah!").then(() =>
          this.props.dispatch(getkategoriList())
        )
      )
      .catch((err) =>
        gagal("Data Gagal Diubah!")
          .then(() => this.props.dispatch(finishSend()))
          .then(() => this.props.dispatch(hideModal()))
      );
  }
  handleReactive(err, kode, data) {
    this.props.dispatch(hideModal());
    let error = err.response.data;
    let check = error.includes("hapus");
    check
      ? reactive(
          err,
          kode,
          "kategori/reactive/",
          data,
          "kategori/update/by-kode-kategori/"
        ).then(() => this.props.dispatch(getkategoriList()))
      : gagal("Data Gagal Ditambahkan");
  }
  tambahModal() {
    this.props.dispatch(showModal());
    this.setState({ isEdit: false });
  }
  editModal() {
    this.props.dispatch(showModal());
    this.setState({ isEdit: true });
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <ol className="breadcrumb mb-2">
          <Link to="/KlasifikasiBarang">
            <button
              type="button"
              className="btn btn-secondary"
              data-tut="reactour_4"
            >
              <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Back
            </button>
          </Link>
        </ol>
        <TourGuide
          tourConfig={[
            `Untuk Menambahkan Data Kategori, Tekan Tombol ini Dan Isi kolom yang disediakan`,
            `Ini adalah Data Kategori yang sudah tersimpan`,
            `Ini adalah tombol untuk menghapus data pada baris yang dipilih`,
            `Ini adalah tombol navigasi untuk kembali`,
          ]}
        />
        <div className="row">
          <div className="col-md-12">
            <div className="card">
              <HeaderCard title="Kategori" />
              <div className="card-body">
                <ModalMasterData
                  content={
                    this.state.isEdit ? (
                      <FormKategoriEdit
                        onSubmit={(data) => this.handleEdit(data)}
                        onSend={this.props.onSend}
                      />
                    ) : (
                      <FormKategori
                        onSubmit={(data) => this.handleSubmit(data)}
                        onSend={this.props.onSend}
                      />
                    )
                  }
                  title={
                    this.state.isEdit
                      ? "Edit Data Kategori"
                      : "Tambah Data Kategoris"
                  }
                />
                <div className="col-lg-12 col-md-12">
                  <div className="text-right">
                    <button
                      name="simpan"
                      id="simpan"
                      type="button"
                      className="btn btn-info"
                      onClick={() => this.tambahModal()}
                      data-tut="reactour_1"
                    >
                      Tambah Data Kategori
                    </button>
                  </div>
                </div>
                <div data-tut="reactour_2">
                  {this.props.kategoriList ? (
                    <ToolkitProvider
                      keyField="kodeKategori"
                      data={this.props.kategoriList}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="form-group mt-5 mb-3">
                          <div className="text-right">
                            <SearchBar
                              {...props.searchProps}
                              className="form-control"
                              placeholder="Pencarian"
                              style={{ margin: 0, top: "50%" }}
                            />
                          </div>
                          <BootstrapTable
                            {...props.baseProps}
                            striped
                            hover
                            condensed
                            bordered={true}
                            headerClasses="header-table"
                            pagination={paginationFactory()}
                          />
                          <ExportCSVButton
                            {...props.csvProps}
                            className="btn btn-primary"
                          >
                            Export CSV
                          </ExportCSVButton>
                        </div>
                      )}
                    </ToolkitProvider>
                  ) : (
                    <div>
                      {this.props.error ? (
                        <div className="text-center">
                          <img
                            src={notrespond}
                            style={{ width: "30%" }}
                            alt="ERROR GET DATA"
                          />
                          <br />
                          <h4>Request API Gagal, Silahkan Refresh</h4>
                        </div>
                      ) : (
                        <Skeleton width={"100%"} height={180} />
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(Kategori);
